.form{
    border:1px solid #000;
    padding: 10px;
    width: 300px;
    border-radius: 4px;
    margin-bottom: 20px;
    display: flex;
        flex-direction: column;
        gap: 10px;
    
}
.item{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.item:not(:last-child){
    margin-bottom: 20px;
}
.label{
    font-size: 20px;
    font-weight: 500;
}
.button{
    padding: 5px;
}
.error{
    padding: 5px;
    border: 1px solid #000;
    background-color: #fff;
    max-width: 100%;
}
